import { uniqueId } from 'lodash';
import React from 'react';
import Swiper from 'react-id-swiper';
import './_swiper.sass';
import imgagesPaths from '../../assets/data/images';

const mySwiper = () => {
  const params = {
    freeMode: true,
    loop: true,
    breakpoints: {
      1366: {
        spaceBetween: -455,
      },
      1024: {
        spaceBetween: -window.innerWidth / 4,
      },
      980: {
        spaceBetween: -250,
      },
      768: {
        spaceBetween: -120,
      },
      640: {
        spaceBetween: -100,
      },
      420: {
        spaceBetween: -80,
      },
      320: {
        spaceBetween: -60,
      },
    },
  };
  return (
    // eslint-disable-next-line react/jsx-props-no-spreading
    <Swiper {...params}>
      {
        imgagesPaths.map(([path, alt]) => (
          <div key={uniqueId()} className="swiper-element">
            <img className="swiper-element__img" src={path} alt={alt} />
          </div>
        ))
      }
    </Swiper>
  );
};

export default mySwiper;
