import cn from 'classnames';
import React from 'react';
import './index.sass';

export default class Video extends React.Component {
  constructor(props) {
    super(props);
    this.state = { flag: 'stop' };
  }

  handlePlayVideo = () => {
    const { flag } = this.state;
    this.setState({ flag: flag === 'stop' ? 'play' : 'stop' });
    document.querySelector('video').play();
  };

  render() {
    const { flag } = this.state;
    const videoPlayClassNames = cn({
      video__play: true,
      hide: flag === 'play',
    });
    return (
      <div className="video">
        <video muted controls={flag === 'play'}>
          <source muted src="./assets/video/exampleVideo.mp4" type="video/mp4" />
        </video>
        <div
          onClick={this.handlePlayVideo}
          onKeyDown={() => {}}
          tabIndex="0"
          role="button"
          label="video"
          className={videoPlayClassNames}
        />
      </div>
    );
  }
}
