import cn from 'classnames';
import React from 'react';
import './_desingCard.sass';

export default class DesingCard extends React.Component {
  constructor(props) {
    super(props);
    this.myVideoRef = React.createRef();
    this.state = { isHoverCard: false };
  }

  handleMouseEnter = () => {
    if (window.innerWidth > 768) {
      this.setState({ isHoverCard: true });
      this.myVideoRef.current.play();
    }
  }

  handleMouseLeave = () => {
    if (window.innerWidth > 768) {
      this.setState({ isHoverCard: false });
      this.myVideoRef.current.pause();
    }
  }

  render() {
    const { name, videoPath, imgPath } = this.props;
    const { isHoverCard } = this.state;

    const topTextClassNames = cn({
      'desing-card__container__top': true,
      top__active: isHoverCard,
    });

    const downloadTextClassNames = cn({
      'desing-card__container__bottom__download-text': true,
      'download-text__active': isHoverCard,
    });

    const videoBgClassNames = cn({
      'desing-card-bg': true,
      bg__active: isHoverCard,
    });

    const cardStyle = {
      backgroundImage: `url("${imgPath}")`,
    };

    return (
      <div
        className="desing-card"
        onMouseEnter={this.handleMouseEnter}
        onMouseLeave={this.handleMouseLeave}
        style={window.innerWidth <= 768 ? cardStyle : null}
      >
        <div className="desing-card__container">
          <div className={topTextClassNames}>
            <h3 className="desing-card__container__top__name">{name}</h3>
          </div>
          <div className={videoBgClassNames} />
          <video ref={this.myVideoRef} className="desing-card__container__video" muted>
            <source src={videoPath} type="video/mp4" />
          </video>
          <div className="desing-card__container__bottom">
            <div className={downloadTextClassNames}>
              <a href="!#">DOWNLOAD ON BRAND PORTAL</a>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
