import cn from 'classnames';
import React from 'react';
import './index.sass';

let lastScrollY = 0;
let isScrollUp = false;

export default class Header extends React.Component {
  constructor(props) {
    super(props);
    this.state = { isShowHeader: false };
  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll, true);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  }

  handleScroll = () => {
    const { isShowHeader } = this.state;
    isScrollUp = lastScrollY > window.scrollY && window.scrollY > 100;
    if (isShowHeader !== isScrollUp) {
      this.setState({ isShowHeader: isScrollUp });
    }
    lastScrollY = window.scrollY;
  };

  render() {
    const { isShowHeader } = this.state;

    const headerClassNames = cn({
      header: true,
      header__active: isShowHeader,
    });

    return (
      <header className={headerClassNames}>
        <div className="header__container">
          <div className="header__container__logo" />
          <div className="header__container__link">
            <a href="https://www.kaspersky.com/" className="header__container__link__link">
              BRAND PORTAL
            </a>
          </div>
        </div>
      </header>
    );
  }
}
