import AOS from 'aos';
import React from 'react';
import './index.sass';
import Swiper from './_swiper';

export default class Examples extends React.Component {
  constructor(props) {
    super(props);
    this.follower = React.createRef();
    this.middle = React.createRef();
  }

  componentDidMount() {
    AOS.init();
  }

  mouseX = (e) => e.clientX;

  mouseY = (e) => e.clientY;

  positionElement = (e) => {
    const follower = this.follower.current;

    const mouse = {
      x: this.mouseX(e),
      y: this.mouseY(e),
    };

    follower.style.top = `${mouse.y + 5}px`;
    follower.style.left = `${mouse.x + 5}px`;
    follower.style.display = 'flex';
  }

  handleMouseMove = (e) => {
    e.persist();
    this.positionElement(e);
  }

  handleMouseEnter = () => {
    this.follower.current.style.display = 'flex';
    this.middle.current.className = 'cursor__middle cursor__middle__animate';
  }

  handleMouseLeave = () => {
    this.follower.current.style.display = 'none';
    this.middle.current.className = 'cursor__middle';
  }

  render() {
    return (
      <div className="examples">
        <div
          className="examples__container"
          onMouseEnter={this.handleMouseEnter}
          onMouseLeave={this.handleMouseLeave}
          onMouseMove={this.handleMouseMove}
        >
          <div ref={this.follower} className="cursor">
            <div className="cursor__left" />
            <div ref={this.middle} className="cursor__middle" />
            <div className="cursor__right" />
          </div>
          <h2 data-aos="fade-up" className="examples__container__title">
            Examples
          </h2>
          <div
            data-aos="fade-left"
            className="examples__container__swiper"
          >
            <Swiper />
          </div>
        </div>
      </div>
    );
  }
}
