import cn from 'classnames';
import React from 'react';
import './_card.sass';

export default class Card extends React.Component {
  constructor(props) {
    super(props);
    this.state = { isHoverCard: false };
  }

  handleMouseEnter = () => {
    if (window.innerWidth > 768) {
      this.setState({ isHoverCard: true });
    }
  }

  handleMouseLeave = () => {
    if (window.innerWidth > 768) {
      this.setState({ isHoverCard: false });
    }
  }

  render() {
    const { name, date, preview } = this.props;
    const { isHoverCard } = this.state;

    const previewStyle = {
      backgroundImage: `url(${preview})`,
    };

    const downloadTextClassNames = cn({
      'card__container__bottom__download-text': true,
      'download-text__active': isHoverCard,
    });

    const downloadIconClassNames = cn({
      'card__container__bottom__download-icon': true,
      'download-icon__active': isHoverCard,
    });

    const previewClassNames = cn({
      'card__container__preview': true,
      'preview__active': isHoverCard,
    });

    return (
      <div
        data-aos="fade-up"
        className="card"
        onMouseEnter={this.handleMouseEnter}
        onMouseLeave={this.handleMouseLeave}
      >
        <div className="card__container">
          <div className="card__container__top">
            <h3 className="card__container__top__name">{name}</h3>
            <span className="card__container__top__date">{`Last updated ${date}`}</span>
          </div>
          <div className="card__container__bottom">
            <div className={downloadTextClassNames}>
              <a href="!#">DOWNLOAD</a>
            </div>
            <div className={downloadIconClassNames}></div>
          </div>
          <div className={previewClassNames}>
            <a href="!#" className="card__container__preview__link">
              <div className="card__container__preview__link__icon" style={previewStyle}></div>
            </a>
          </div>
        </div>
      </div>
    );
  }
};
