import React from 'react';
import Maket from './_maket';
import './index.sass';

export default class Concept extends React.Component {
  constructor(props) {
    super(props);
    this.container = React.createRef();
    this.containerIsometric = React.createRef();
    this.state = {
      conceptAtimateSteps: {
        step1: false,
        step2: false,
        step3: false,
      },
    };
  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll, true);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  }

  handleScroll = () => {
    const top = this.container.current.offsetTop;
    const height = this.containerIsometric.current.offsetHeight;
    const {
      conceptAtimateSteps: { step1, step2, step3 },
    } = this.state;

    const step1Distance = top;
    const step2Distance = top + height / 4;
    const step3Distance = top + (height / 4) * 1.5;
    const isStep1Run = window.pageYOffset >= step1Distance && !step1 && !step2 && !step3;
    const isStep2Run = window.pageYOffset >= step2Distance
      && step1 && !step2 && !step3 && window.innerWidth > 639;
    const isStep3Run = window.pageYOffset >= step3Distance
      && step1 && step2 && !step3 && window.innerWidth > 639;
    const isStep3Back = window.pageYOffset <= step1Distance && step1 && !step2 && !step3;
    const isStep2Back = window.pageYOffset <= step2Distance && step1 && step2 && !step3;
    const isStep1Back = window.pageYOffset <= step3Distance && step1 && step2 && step3;

    if (isStep1Run) this.setState({ conceptAtimateSteps: { step1: true, step2, step3 } });
    if (isStep2Run) this.setState({ conceptAtimateSteps: { step1, step2: true, step3 } });
    if (isStep3Run) this.setState({ conceptAtimateSteps: { step1, step2, step3: true } });
    else {
      if (isStep3Back) this.setState({ conceptAtimateSteps: { step1: false, step2, step3 } });
      if (isStep2Back) this.setState({ conceptAtimateSteps: { step1, step2: false, step3 } });
      if (isStep1Back) this.setState({ conceptAtimateSteps: { step1, step2, step3: false } });
    }
  }

  render() {
    const { conceptAtimateSteps } = this.state;
    return (
      <div onWheel={this.handleWheel} ref={this.container} className="concept">
        <div className="concept__container">
          <h2 data-aos="fade-up" className="concept__container__title">
            Engineered for immunity
          </h2>
          <div data-aos="fade-up" className="concept__container__top">
            <div className="concept__container__top__right">
              <span className="concept__container__top__right__line">
                _______
              </span>
              <p className="concept__container__top__right__text">
                The core principle of the identity system is
                centered around two distinct layers: ‘engineered’ and ‘immunity’.
              </p>
            </div>
          </div>
          <div ref={this.containerIsometric} className="concept__container__isometric">
            <Maket conceptAtimateSteps={conceptAtimateSteps} />
          </div>
          <div className="concept__container__step2">
            <h3 className="concept__container__step2__title">
              Engineered
            </h3>
            <span className="concept__container__step2__line">
              _______
            </span>
            <p className="concept__container__step2__text">
              A structured, functional layer
              {' '}
              <br />
              {' '}
              with an engineered feel.
            </p>
            <div className="concept__container__step2__img" />
          </div>
          <div className="concept__container__step3">
            <h3 className="concept__container__step3__title">
              Immunity
            </h3>
            <span className="concept__container__step3__line">
              _______
            </span>
            <p className="concept__container__step3__text">
              A vibrant immunity
              {' '}
              <br />
              {' '}
              field creates a secure space.
            </p>
            <div className="concept__container__step3__img" />
          </div>
        </div>
      </div>
    );
  }
}
