import cn from 'classnames';
import React from 'react';
import './index.sass';

let lastScrollY = 0;
let flag = false;

export default class Logo extends React.Component {
  constructor(props) {
    super(props);
    this.myRef = React.createRef();
    this.state = { isLogoAnimate: false };
  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll, true);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  }

  handleScroll = () => {
    const logoTop = this.myRef.current.offsetTop;
    lastScrollY = window.pageYOffset;

    if (lastScrollY >= logoTop && !flag) {
      this.setState({ isLogoAnimate: true });
      flag = true;
    } else if (lastScrollY <= logoTop && flag) {
      this.setState({ isLogoAnimate: false });
      flag = false;
    }
  };

  render() {
    const { isLogoAnimate } = this.state;

    const classNamesLogoBg = cn({
      logo__bg: true,
      logo__bg__active: isLogoAnimate,
      logo__bg__notActive: !isLogoAnimate,
    });

    const classNamesMainLogo = cn({
      'logo__container__main-logo': true,
      'main-logo__active': isLogoAnimate,
      'main-logo__notActive': !isLogoAnimate,
    });

    const classNamesMainLogoGreen = cn({
      'logo__container__main-logo-green': true,
      'main-logo-green__active': isLogoAnimate,
      'main-logo-green__notActive': !isLogoAnimate,
    });

    const classNamesMainLogoSchema = cn({
      'logo__container__main-logo-schema': true,
      'main-logo-schema__active': isLogoAnimate,
      'main-logo-schema__notActive': !isLogoAnimate,
    });

    const classNamesDownLoad = cn({
      logo__container__top__nav__element: true,
      nav__element__active: isLogoAnimate,
      nav__element__notActive: !isLogoAnimate,
    });

    return (
      <div ref={this.myRef} className="logo">
        <div className="logo__container">
          <div data-aos="fade-up" className="logo__container__top">
            <nav className="logo__container__top__nav">
              <a href="!#" className="logo__container__top__nav__element">NEW LOGOTYPE</a>
              <a href="!#" className={classNamesDownLoad}>DOWNLOAD</a>
            </nav>
          </div>
          <div className={classNamesMainLogo} />
          <div className={classNamesMainLogoGreen} />
          <div className={classNamesMainLogoSchema} />
        </div>
        <div className={classNamesLogoBg} />
      </div>
    );
  }
}
